* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.body-container{
  /* padding-top:50px; */
}


.user-name{
  font-weight: bold;
}

.user-name img{
  margin-right: 5px;

}

.user-name:hover{
  cursor: pointer;
}

.edit-form{
  /* border: 1px solid blue; */
  /* padding:50px; */
  margin-top: 100px;
  height: 500px;
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding:20px;
  border: 1px solid grey;
  border-radius: 20px;
  box-shadow: 5px 10px #888888;
    
}
.edit-form-container{

  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.setting-section{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;

}


.profile-picture{
  height:100px;
}

.navbar-container{
  /* position: fixed; */
  background:rgb(32,	47	,62	);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
  width:100%;
  flex-wrap: nowrap;
  flex-shrink: 1;

}

/* navbar */
.navbar-container a{
  color: white;
  text-decoration: none;
  height:40px;
  width: 120px;
  text-align: center;
  padding-top: 10px;
}

.navbar-container a:hover{
  background: black;
}






/* homepage */

.homepage-container{
  padding-top: 50px;
  height: 90vh;
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;


}

.homepage-tweets{
  border: 1px solid black;
  height: 80%;
  padding:20px;
  border-radius: 20px;
  box-shadow: 5px 10px #888888;
  overflow: scroll;
}

.profile-tweets{
  border: 1px solid black;
  height: 500px;
  padding:20px;
  border-radius: 20px;
  box-shadow: 5px 10px #888888;
  overflow: scroll;
  width: 500px;
}

.profile-blogs{
  border: 1px solid black;
  height: 300px;
  padding:20px;
  border-radius: 20px;
  box-shadow: 5px 10px #888888;
  overflow: scroll;
  width: 500px;
}

.profile-blogs-and-form{
display: flex;
flex-direction: column;
align-items: center;
}

.profile-container{
  padding:10px;
}

.blog-and-tweet{
  padding-top:40px; 
 display: flex;
 flex-direction: row;
 justify-content: space-around
}


.each-tweet{
  width:80%;
  margin: 10px auto;
  border-radius: 20px;
  padding:10px;

}

.each-tweet:nth-child(even){
  background-color:  rgb(217,	218,	213	);
}

.each-profile-pic{
  height:70px;
  padding:10px;
}

.each-profile-pic:hover{
  opacity: 1;
  -webkit-animation: flash 1.5s;
  animation: flash 1.5s;
  cursor: pointer;
}

.homepage-tweets{
  width:400px;

}

.modale{
  position: fixed;
  width: 50%;
  z-index: 999;
  background: grey;
  top: 20%;
  left: 20%;
}

.modale header {
  padding: 10px;
  text-align: right;
}


