.side-bar{
  position: fixed;
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  width: 300px;
  padding: 15px;
  height: 93vh;
  border: 1px solid black;
  right:20px;
  top: 50px;
  border-radius: 20px;
  box-shadow: 5px 10px #888888;
}

.two-user-messages, .global-messages{
  padding: 25px;
  height: 100px;
  overflow: scroll;

}


.Calender{
  padding:15px;
}

.calender-flex{
  width:100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.create-event{
  width: 100px;
  
}

.event-submit-btn{
  display: flex;
  justify-content: center;
}
