  Single Chat Component
***********************/

.chat-single , .chat-global {
  background: white;
  height: 300px;
  border: 1px solid grey;
  overflow: scroll;
  align-content: center;
}

.chat-single form , .chat-global form {
  width: 80%;
  /* margin: 40px auto 0; */
}

.chat-single input, .chat-global form {
  text-align: center;
